import React, { Component } from 'react'
import * as THREE from 'three'
import OrbitContructor from 'three-orbit-controls'
import StartAudioContext from 'startaudiocontext'
import deepAssign from 'deep-assign'

// Config
import Config from './Config'
import Detector from './libs/Detector'

// Geometry
import Box from './geometry/box/Box'

// CSS
import './App.css'

class App extends Component {
  constructor (props) {
    super(props)
    this.config = deepAssign(Config, this.props.config)
    this.detector = new Detector()
    this.OrbitControls = OrbitContructor(THREE)
    this.fftBandCount = 64
    this.soundDisabled = this.detector.isSafari && !this.detector.isChrome
  }

  componentDidMount () {
    this.initStage()
  }

  initStage () {
    this.initScene()
    this.initCamera()
    this.initRenderer()
    this.initControls()
    if (!this.soundDisabled) {
      this.initSound().then(audioBuffer => {
        this.playSound(audioBuffer)
      })
    }
    this.initLights()
    this.initGeometry()
    this.addEvents()
    this.animate()
  }

  initLights () {
    let light = new THREE.AmbientLight(0xffffff)
    this.scene.add(light)

    let pointLight = new THREE.PointLight(0xffffff, 10)
    pointLight.position.set(0, -50, -100)
    this.scene.add(pointLight)

    let pointLight2 = new THREE.PointLight(0xffffff, 10)
    pointLight2.position.set(0, 50, 100)
    this.scene.add(pointLight2)
  }

  initGeometry () {
    this.box = new Box().create(this.fftBandCount)
    this.scene.add(this.box)
  }

  initAudioContext () {
    window.AudioContext = window.AudioContext || window.webkitAudioContext
    if (window.AudioContext) {
      this.audioContext = new window.AudioContext()
    }

    StartAudioContext(this.audioContext, '#body', function () {

    })
  }

  initSound () {
    return new Promise((resolve, reject) => {
      this.initAudioContext()

      this.analyser = this.audioContext.createAnalyser()
      this.analyser.fftSize = 128
      this.analyser.smoothingTimeConstant = 0.95
      this.frequencyData = new Uint8Array(this.analyser.frequencyBinCount)

      window.fetch('https://symphony.iohk.io/static/assets/sounds/ambience/mining.mp3')
        .then(response => response.arrayBuffer())
        .then(arrayBuffer => this.audioContext.decodeAudioData(arrayBuffer))
        .then(audioBuffer => {
          resolve(audioBuffer)
        })
    })
  }

  playSound (audioBuffer) {
    const source = this.audioContext.createBufferSource()
    source.buffer = audioBuffer
    source.loop = true
    source.connect(this.analyser)

    this.gainNode = this.audioContext.createGain()

    source.connect(this.gainNode)

    this.gainNode.connect(this.audioContext.destination)
    this.gainNode.gain.value = 0.0

    source.start()
  }

  initControls () {
    this.controls = new this.OrbitControls(this.camera, this.renderer.domElement)
    this.setControlsSettings()
  }

  setControlsSettings () {
    this.controls.minDistance = 140
    this.controls.maxDistance = 160
    this.controls.enablePan = false
    this.controls.autoRotate = this.config.scene.autoRotate
    this.controls.autoRotateSpeed = this.config.scene.autoRotateSpeed
    this.controls.zoomSpeed = 0.7
    this.controls.rotateSpeed = 0.07
    this.controls.enableDamping = true
    this.controls.enableZoom = false
    this.controls.dampingFactor = 0.04
    this.controls.minPolarAngle = 1.0
    this.controls.maxPolarAngle = (Math.PI * 0.6)
  }

  setConfig (newConfig) {
    this.config = deepAssign(this.config, newConfig)

    this.setControlsSettings()
    this.setCameraSettings()
  }

  animate () {
    window.requestAnimationFrame(this.animate.bind(this))
    this.renderFrame()
  }

  renderFrame () {
    this.controls.update()

    if (!this.soundDisabled) {
      this.analyser.getByteFrequencyData(this.frequencyData)
      this.box.geometry.attributes.fftData.array = this.frequencyData
      this.box.geometry.attributes.fftData.needsUpdate = true
    }

    this.renderer.render(this.scene, this.camera)
  }

  addEvents () {
    window.addEventListener('resize', this.resize.bind(this), false)
    this.resize()
  }

  initScene () {
    this.scene = new THREE.Scene()
    this.scene.rotation.z = 0.3
    this.scene.rotation.y = Math.PI
  }

  /**
   * Set up camera with defaults
   */
  initCamera () {
    this.camera = new THREE.PerspectiveCamera(50, window.innerWidth / window.innerHeight, 0.1, 20000)
    this.camera.position.y = -30

    this.camera.updateMatrixWorld()
  }

  /**
   * Set up renderer
   */
  initRenderer () {
    this.renderer = new THREE.WebGLRenderer({
      antialias: this.config.scene.antialias,
      canvas: document.getElementById(this.config.scene.canvasID),
      alpha: true
    })
    this.renderer.setClearColor(0xffffff, 0)
    this.renderer.setPixelRatio(window.devicePixelRatio)
  }

  /**
   * Window resize
   */
  resize () {
    if (this.config.scene.fullScreen) {
      this.width = window.innerWidth
      this.height = window.innerHeight
    } else {
      this.width = this.config.scene.width
      this.height = this.config.scene.height
    }

    this.config.scene.width = this.width
    this.config.scene.height = this.height

    this.camera.aspect = this.width / this.height
    this.camera.updateProjectionMatrix()
    this.renderer.setSize(this.width, this.height, false)
  }

  render () {
    return (
      <div className='Logo'>
        <canvas width={this.config.scene.width} height={this.config.scene.height} id={this.config.scene.canvasID} />
      </div>
    )
  }
}

export default App



